.container-panel-edit-modality{height:100vh;box-sizing:border-box}.container-edit-modality{width:100%;padding:20px 10px;box-sizing:border-box}.container-edit-modality__back{width:100%;max-width:1200px;height:60px;margin:0 auto;display:flex;flex-direction:row;justify-content:flex-start;align-items:center;box-sizing:border-box}.container-edit-modality__back .container-btn-back{width:80px;height:40px;display:flex;flex-direction:row;justify-content:center;align-items:center;box-shadow:0px 0px 5px rgba(0,0,0,.5);cursor:pointer}.container-edit-modality__back .container-btn-back img{width:40px}.container-edit-modality__title{width:100%;max-width:1200px;margin:0 auto;margin-top:20px;display:flex;flex-direction:row;justify-content:center;align-items:center;background-color:#fff6c3;border-color:#d4d4d4}.container-edit-modality__title h2{color:#3d3d3d;font-family:"Open Sans",sans-serif;font-weight:600}.container-edit-modality__board{width:80%;max-width:800px;margin:0 auto;margin-top:20px;padding:20px;box-shadow:0px 0px 5px rgba(0,0,0,.5);box-sizing:border-box}.container-edit-modality__board .item-modality-board{width:100%;margin-bottom:20px;display:flex;flex-direction:column;justify-content:center;align-items:flex-start}.container-edit-modality__board .item-modality-board label{color:#6c757d;font-weight:400;line-height:1.125;font-family:Roboto,"Helvetica Neue",sans-serif;font-size:12px}.container-edit-modality__board .item-modality-board input{width:100%;padding:5px;margin-top:2px;border:none;border-bottom:1px solid rgba(0,0,0,.42);font-family:Arial,Helvetica,sans-serif;outline:none;box-sizing:border-box}.container-edit-modality__board .btn-edit-modality{width:100%;padding-top:20px;box-sizing:border-box}.container-edit-modality__board .btn-edit-modality button{padding:10px 20px;color:#fff;background-color:#3f51b5;border-radius:5px;font-family:Roboto,"Helvetica Neue",sans-serif;font-size:14px;font-weight:500;border:none;outline:none;box-sizing:border-box;cursor:pointer}@media screen and (min-width: 319px)and (max-width: 766px){.container-edit-modality__board .btn-edit-modality button{width:100%}}
